.singleVideoWrapper {
  width: 100%;
  max-width: 350px; /* Ensures consistent card size */
  min-width: 280px; /* Prevents shrinking too much */
  background: #fff;
  padding: 10px;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  text-align: left;
}

.videoThumbnail img {
  width: 100%;
  border-radius: 10px;
}

.videoInfoWrapper {
  padding: 10px;
}

.videoTitle {
  font-size: 1rem;
  font-weight: bold;
}

.videoDesc {
  font-size: 0.9rem;
  color: #666;
}
