*{
    padding: 0;
    margin: 0;
    font-family: Arial, Helvetica, sans-serif;
}

img{
    width: 100%;
}


.header-wrapper{
    background-color:rgb(30,30,30);
    opacity: 0.8;
    height: 43px;
    position: fixed;
    
    top: 0;
    width: 100%;
}

.header-wrapper .internal-wrapper .link-wrapper a{
    color: white;
    text-decoration: none;
    opacity: 0.9;
}

.header-wrapper .internal-wrapper .link-wrapper a:hover{
    color: white;
    opacity: 1;
}

.internal-wrapper ul{
    display: flex;
    justify-content: space-between;
    list-style: none;
}

.internal-wrapper{
    width: 1450px;
    margin: 0 auto;
    padding: 10px 10px;
    margin-top: 4px;
}

.header-wrapper .internal-wrapper .link-wrapper li img{
   width: 13px;
}

.header-wrapper .internal-wrapper ul li{
    padding: 15px 0 px;
    height: 25px;
    font-size: 13px;
}

.header-wrapper .internal-wrapper ul li .home-logo{
    padding: 12px 0 8px;
}

.header-wrapper .internal-wrapper ul li .cart-icon{
    padding: 12px 0 8px;
}

.header-wrapper .internal-wrapper ul li .search-icon{
    padding: 14px 0 8px;
}

.internal-wrapper .link-wrapper{
    width: 981px;
    height: 18px;
    margin: 0 auto;
}