*{
    padding: 0;
    margin: 0;
    font-family: Arial, Helvetica, sans-serif;
}

img{
    width: 100%;
}

.section-1-wrapper{
   background-image: url("../RESOURCES /iphone-15-pro.jpeg");
   background-position: center;
   background-attachment: scroll;
   background-size: cover;
   height: 692px;
}

.all-items-wrapper{
    text-align: center;
    padding-top: 80px;
}

.ihone-15-pro{
    color: white;
    font-size: 34px;
    
}

.titanium{
    color: white;
    font-size: 30px;
    margin-top: 6px;
}

.two-link-wrapper ul li {
   display: inline-block;
   list-style: none;
   justify-content: space-between;
   margin-left: 27px;
   margin-top: 10px;
}

.two-link-wrapper ul li a{
    color: rgb(41,151,255);
    text-decoration: none;
    font-size: 25px;
}

.two-link-wrapper ul li a:hover{
   text-decoration: underline;
}