  *{
    padding: 0;
    margin: 0;
    font-family: Arial, Helvetica, sans-serif;
}

img{
    width: 100%;
}
  
  .mac-div{
    background-image: url("../RESOURCES /mac.jpg");
    background-position: center;
    background-attachment: scroll;
    background-size: cover;
    height: 582px;
    width: 944px;
   }
   .rihanna-div{
    background-image: url("../RESOURCES /rihanna.jpg");
    background-position: center;
    background-attachment: scroll;
    background-size: cover;
    height: 582pxpx;
    width: 944px;
   }

   .all-items-wrapper-5{
    display: flex;
    text-align: center;
    text-decoration: none;
   }

   .all-items-wrapper-5 .rihanna-div{
    padding-top: 60px;
    margin-left: 0px;
    margin-right: 10px;
   }

   .all-items-wrapper-5 .mac-div{
    padding-top: 60px;
    margin-right: 10px;
    margin-left: 20px;
   }

   .all-items-wrapper-5 .rihanna-div img{
      width: 177px;
      height: 32px;
   }

   .all-items-wrapper-5 .mac-div .mind-blowing p{
    color: black;
    font-size: 22px;
    padding-top: 7px;
   }

   .all-items-wrapper-5 .mac-div .macbook h2{
     font-size: 40px;
   }

   .all-items-wrapper-5 .rihanna-div .two-link-wrapper-5 ul li{
    display: inline-block;
    padding-left: 25px;
    padding-top: 10px;
    list-style: none;
   }

   .all-items-wrapper-5 .rihanna-div .two-link-wrapper-5 ul li a{
     text-decoration: none;
     color: white;
   }


   .all-items-wrapper-5 .mac-div .two-link-wrapper-5 ul li{
    display: inline-block;
    padding-left: 25px;
    padding-top: 10px;
    list-style: none;
   }

   .all-items-wrapper-5 .mac-div .two-link-wrapper-5 ul li a{
    text-decoration: none;
    color:  rgb(0,102,204);
  }

  .all-items-wrapper-5 .rihanna-div .work-out p{
    font-size: 23px;
  }

  .all-items-wrapper-5 .rihanna-div .music-by p{
    font-size: 21px;
  }

  .all-items-wrapper-5 .mac-div .two-link-wrapper-5 ul li a:hover{
     text-decoration: underline;
  }

  .all-items-wrapper-5 .rihanna-div .two-link-wrapper-5 ul li a:hover{
    text-decoration: underline;
 }